* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #0d1117;
  min-height: 100vh;
}

.app {
  min-height: 100vh;
  padding: 20px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

/* Header */
.header {
  text-align: center;
  color: white;
  margin-bottom: 40px;
}

.header h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  font-weight: 700;
  color: #58a6ff;
}

.header p {
  font-size: 1.1rem;
  color: #8b949e;
}

/* Content */
.content {
  background: #161b22;
  border-radius: 8px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  border: 1px solid #30363d;
}

/* Input Section */
.input-section {
  padding: 40px;
  background: #1c2128;
  border-bottom: 1px solid #30363d;
}

.input-section h2 {
  color: #c9d1d9;
  margin-bottom: 30px;
  font-size: 1.8rem;
}

.spending-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.input-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.input-group label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  color: #8b949e;
  font-size: 0.95rem;
}

.category-icon {
  font-size: 1.3rem;
}

.category-label {
  flex: 1;
}

.input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.currency-symbol {
  position: absolute;
  left: 16px;
  color: #58a6ff;
  font-weight: 600;
  pointer-events: none;
  font-size: 1.1rem;
}

.input-wrapper input {
  width: 100%;
  padding: 14px 16px 14px 32px;
  border: 1px solid #30363d;
  border-radius: 6px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: #0d1117;
  color: #c9d1d9;
}

.input-wrapper input:focus {
  outline: none;
  border-color: #58a6ff;
  box-shadow: 0 0 0 3px rgba(88, 166, 255, 0.3);
}

.input-wrapper input::placeholder {
  color: #484f58;
}

/* Buttons */
.button-group {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.btn {
  padding: 14px 32px;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.btn-primary {
  background: #238636;
  color: white;
}

.btn-primary:hover:not(:disabled) {
  background: #2ea043;
}

.btn-primary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-secondary {
  background: transparent;
  color: #58a6ff;
  border: 1px solid #30363d;
}

.btn-secondary:hover {
  background: #1c2128;
  border-color: #58a6ff;
}

/* Error Message */
.error-message {
  background: rgba(248, 81, 73, 0.1);
  border: 1px solid #f85149;
  border-radius: 6px;
  padding: 12px 16px;
  color: #ff7b72;
  margin-bottom: 20px;
  font-weight: 500;
}

/* Results Section */
.results-section {
  padding: 40px;
  background: #161b22;
  animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.summary-card {
  background: #1c2128;
  color: white;
  border-radius: 8px;
  padding: 30px;
  margin-bottom: 30px;
  border: 1px solid #30363d;
}

.summary-card h3 {
  margin-bottom: 20px;
  font-size: 1.5rem;
  color: #58a6ff;
}

.summary-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 20px;
}

.stat {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.stat-label {
  font-size: 0.9rem;
  color: #8b949e;
}

.stat-value {
  font-size: 2rem;
  font-weight: 700;
  color: #c9d1d9;
}

/* Category Breakdown */
.category-breakdown-summary {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #30363d;
}

.category-breakdown-summary h4 {
  color: #58a6ff;
  margin-bottom: 15px;
  font-size: 1.1rem;
}

.category-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.category-chip {
  background: rgba(88, 166, 255, 0.1);
  border: 1px solid rgba(88, 166, 255, 0.3);
  border-radius: 16px;
  padding: 8px 16px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.chip-label {
  color: #8b949e;
  font-size: 0.9rem;
}

.chip-amount {
  color: #58a6ff;
  font-weight: 700;
}

/* Strategy Toggle */
.strategy-toggle {
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  background: #1c2128;
  padding: 6px;
  border-radius: 8px;
  border: 1px solid #30363d;
}

.toggle-btn {
  flex: 1;
  padding: 12px 24px;
  background: transparent;
  border: none;
  border-radius: 6px;
  color: #8b949e;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.toggle-btn.active {
  background: #238636;
  color: white;
}

.toggle-btn:hover:not(.active) {
  background: rgba(88, 166, 255, 0.1);
  color: #58a6ff;
}

/* Multi-Card Strategies */
.multi-card-section h2 {
  color: #58a6ff;
  margin-bottom: 10px;
  font-size: 1.8rem;
}

.section-subtitle {
  color: #8b949e;
  margin-bottom: 30px;
}

.strategy-card {
  background: #1c2128;
  border: 1px solid #30363d;
  border-radius: 8px;
  padding: 24px;
  margin-bottom: 20px;
}

.strategy-header h3 {
  color: #58a6ff;
  margin-bottom: 8px;
  font-size: 1.3rem;
}

.strategy-description {
  color: #8b949e;
  margin-bottom: 20px;
  font-size: 0.95rem;
}

.strategy-summary {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 20px;
  padding: 20px;
  background: #0d1117;
  border-radius: 6px;
}

.strategy-stat {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.strategy-stat .label {
  color: #8b949e;
  font-size: 0.9rem;
}

.strategy-stat .value {
  color: #c9d1d9;
  font-size: 1.5rem;
  font-weight: 700;
}

.strategy-stat .value.negative {
  color: #f85149;
}

.strategy-cards {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.strategy-card-item {
  background: #0d1117;
  border: 1px solid #30363d;
  border-radius: 6px;
  padding: 16px;
  display: flex;
  align-items: center;
  gap: 15px;
}

.card-badge {
  background: #238636;
  color: white;
  padding: 6px 12px;
  border-radius: 12px;
  font-weight: 700;
  font-size: 0.85rem;
  flex-shrink: 0;
}

.card-info {
  flex: 1;
}

.card-info h4 {
  color: #c9d1d9;
  margin-bottom: 6px;
  font-size: 1.1rem;
}

.earning-rate {
  color: #58a6ff;
  font-size: 0.9rem;
  margin-bottom: 8px;
}

.card-quick-stats {
  display: flex;
  gap: 20px;
  color: #8b949e;
  font-size: 0.9rem;
}

/* Individual Card Results */
.results-section > h2 {
  color: #58a6ff;
  margin-bottom: 10px;
  font-size: 1.8rem;
}

.results-subtitle {
  color: #8b949e;
  margin-bottom: 30px;
  font-size: 1rem;
}

.cards-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 40px;
}

.card-item {
  background: #1c2128;
  border: 1px solid #30363d;
  border-radius: 8px;
  padding: 24px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.card-item:hover {
  border-color: #58a6ff;
  box-shadow: 0 4px 16px rgba(88, 166, 255, 0.2);
}

.card-item.top-pick {
  border-color: #d29922;
  background: linear-gradient(135deg, #1c2128 0%, #21262d 100%);
}

.best-badge {
  position: absolute;
  top: 0;
  right: 30px;
  background: #d29922;
  color: #0d1117;
  padding: 8px 20px;
  border-radius: 0 0 8px 8px;
  font-weight: 700;
  font-size: 0.85rem;
  letter-spacing: 0.5px;
}

.card-header {
  display: flex;
  align-items: flex-start;
  gap: 16px;
  margin-bottom: 20px;
}

.card-rank {
  background: #238636;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.1rem;
  flex-shrink: 0;
}

.card-name-section {
  flex: 1;
}

.card-name {
  color: #c9d1d9;
  font-size: 1.3rem;
  margin-bottom: 8px;
}

.earning-rate-display {
  color: #58a6ff;
  font-size: 0.95rem;
  font-weight: 500;
}

.card-stats {
  background: #0d1117;
  border-radius: 6px;
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid #30363d;
}

.stat-row {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid rgba(48, 54, 61, 0.5);
}

.stat-row:last-child {
  border-bottom: none;
}

.stat-row.total {
  border-top: 2px solid #238636;
  margin-top: 8px;
  padding-top: 16px;
  font-weight: 700;
}

.stat-row .stat-label {
  color: #8b949e;
  font-size: 0.95rem;
}

.stat-row .stat-value {
  font-weight: 600;
  font-size: 1.1rem;
  color: #c9d1d9;
}

.stat-row .stat-value.highlight {
  color: #3fb950;
  font-size: 1.3rem;
  font-weight: 700;
}

.stat-row .stat-value.positive {
  color: #3fb950;
}

.credit-note {
  font-size: 0.8rem;
  color: #8b949e;
  font-weight: 400;
}

.rewards-breakdown {
  margin-top: 20px;
}

.rewards-breakdown h4 {
  color: #58a6ff;
  margin-bottom: 12px;
  font-size: 1rem;
}

.breakdown-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 10px;
}

.breakdown-item {
  background: #0d1117;
  border: 1px solid #30363d;
  border-radius: 6px;
  padding: 10px 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.breakdown-category {
  color: #8b949e;
  font-size: 0.85rem;
  text-transform: capitalize;
}

.breakdown-rate {
  color: #58a6ff;
  font-size: 0.9rem;
  font-weight: 600;
}

.breakdown-amount {
  color: #3fb950;
  font-weight: 700;
  font-size: 1rem;
}

/* Info Box */
.info-box {
  background: rgba(28, 33, 40, 0.8);
  border-left: 3px solid #58a6ff;
  border-radius: 6px;
  padding: 20px;
  margin-top: 30px;
  border: 1px solid #30363d;
}

.info-box h4 {
  color: #58a6ff;
  margin-bottom: 12px;
  font-size: 1.1rem;
}

.info-box ul {
  list-style: none;
  padding: 0;
}

.info-box li {
  color: #8b949e;
  margin-bottom: 8px;
  padding-left: 20px;
  position: relative;
  line-height: 1.6;
}

.info-box li:before {
  content: "â†’";
  position: absolute;
  left: 0;
  color: #58a6ff;
  font-weight: 700;
}

.info-box strong {
  color: #c9d1d9;
}

/* Disclaimers */
.disclaimers {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.disclaimer-box {
  background: rgba(187, 128, 9, 0.1);
  border: 1px solid rgba(187, 128, 9, 0.4);
  border-left: 3px solid #bb8009;
  border-radius: 6px;
  padding: 16px 20px;
}

.disclaimer-box p {
  color: #8b949e;
  font-size: 0.9rem;
  line-height: 1.6;
  margin: 0;
}

.disclaimer-box strong {
  color: #c9d1d9;
}

/* Footer */
.footer {
  text-align: center;
  padding: 30px 20px;
  color: #8b949e;
}

.footer p {
  margin-bottom: 8px;
  font-size: 0.95rem;
}

.small-disclaimer {
  font-size: 0.8rem;
  opacity: 0.7;
  font-style: italic;
}

/* Responsive */
@media (max-width: 768px) {
  .header h1 {
    font-size: 2rem;
  }

  .input-section,
  .results-section {
    padding: 24px;
  }

  .spending-grid {
    grid-template-columns: 1fr;
  }

  .summary-stats {
    grid-template-columns: 1fr;
  }

  .stat-value {
    font-size: 1.5rem;
  }

  .card-name {
    font-size: 1.1rem;
  }

  .breakdown-grid {
    grid-template-columns: 1fr;
  }

  .button-group {
    flex-direction: column;
  }

  .btn {
    width: 100%;
  }

  .strategy-toggle {
    flex-direction: column;
  }

  .category-chips {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .app {
    padding: 10px;
  }

  .header h1 {
    font-size: 1.5rem;
  }

  .header p {
    font-size: 0.95rem;
  }

  .input-section h2,
  .results-section > h2 {
    font-size: 1.4rem;
  }
}